import * as firebase from "firebase/app"
import "firebase/messaging"
import Vue from "vue"
import Portal from "@/managers/portal"

class PushNotifications {
	public onMessage: ((payload: IPushNotification) => void) | null = null

	private readonly app!: firebase.app.App
	private readonly messaging!: firebase.messaging.Messaging
	private readonly observable!: {permission: NotificationPermission | null, isEnabled: boolean | null}

	private token: string | null = null

	public get isSupported(): boolean {
		return this.observable.permission !== null
	}

	public get isGranted(): boolean {
		return this.observable.permission === "granted"
	}

	public get isDenied(): boolean {
		return this.observable.permission === "denied"
	}

	public get isReady(): boolean {
		return this.observable.isEnabled !== null
	}

	public get isEnabled(): boolean {
		return this.observable.isEnabled !== null && this.observable.isEnabled
	}

	public set isEnabled(value: boolean) {
		if (value)
			this.enableNotifications()
		else
			this.disableNotifications()
	}

	constructor() {
		this.observable = Vue.observable({permission: null, isEnabled: null})
		try {
			this.app = firebase.initializeApp(JSON.parse(process.env.VUE_APP_FIREBASE_CONFIG as string))
			this.messaging = firebase.messaging()
			this.initializeMessaging()
			if ("Notification" in window) {
				this.observable.permission = Notification.permission
				this.checkState()
					.then(isEnabled => this.observable.isEnabled = isEnabled)
					.catch(reason => {
						this.observable.isEnabled = false
						throw reason
					})
			}
		} catch (error) {
			setTimeout(() => {throw error}, 100)
		}
	}

	public async requestPermission(): Promise<boolean> {
		if (!this.isSupported)
			throw new Error("Notifications are not supported")

		if (Notification.permission === "granted")
			return true

		this.observable.permission = await Notification.requestPermission()

		return this.isGranted
	}

	public async enableNotifications(): Promise<void> {
		if (!this.isReady)
			throw new Error("Not ready to change notifications")

		if (!this.isGranted)
			throw new Error("Permission for notifications have not been granted")

		if (this.isEnabled)
			return

		if (this.token === null)
			this.token = await this.messaging.getToken()

		this.observable.isEnabled = true
		return Portal.client.call.account.pushNotificationDevicesPost(this.token!).response
			.then(
				() => {},
				reason => {
					this.observable.isEnabled = false
					throw reason
				})
	}

	public async disableNotifications(): Promise<void> {
		if (!this.isReady)
			throw new Error("Not ready to change notifications")

		if (this.token === null)
			this.token = await this.messaging.getToken()

		this.messaging.deleteToken(this.token!)

		this.observable.isEnabled = false
		return Portal.client.call.account.pushNotificationDevicesDeleteByToken(this.token!).response
			.then(
				() => {},
				reason => {
					this.observable.isEnabled = true
					throw reason
				})
	}

	public async disableNotificationsOnAllDevices(): Promise<void> {
		await Portal.client.call.account.pushNotificationDevicesDelete()
	}

	public demonstrate(delay: number = 0): Promise<void> {
		return new Promise<void>(resolve => {
			setTimeout(() =>
				Portal.client.call.account.pushNotificationDevicesDemonstrate()
					.response.then(() => resolve())
			, delay * 1000)
		})


	}

	private initializeMessaging(): void {
		this.messaging.usePublicVapidKey(process.env.VUE_APP_FIREBASE_PUBLIC_KEY as string)

		this.messaging.onMessage(payload => {
			if (this.onMessage === null)
				throw new Error("Push notification received, but onMessage is not set!")

			this.onMessage(payload)
		})

		this.messaging.onTokenRefresh(() => {
			this.messaging.getToken()
				.then(token => {
					if (this.isEnabled) {
						if (this.token !== null)
							Portal.client.call.account.pushNotificationDevicesDeleteByToken(this.token)
						Portal.client.call.account.pushNotificationDevicesPost(token!)
					}

					this.token = token
				})
		})
	}

	private async checkState(): Promise<boolean> {
		this.token = await this.messaging.getToken()

		if (this.token === null)
			return false

		try {
			await Portal.client.call.account.pushNotificationDevicesGetByToken(this.token).response
			return true
		} catch (e) {
			this.messaging.deleteToken(this.token)
			this.token = null
			return false
		}
	}
}

export interface IPushNotification {
	data: {
		message: string
		type: string
	}
	from: string
	priority: string
	notification: {
		title: string
		body: string
	}
}

export default new PushNotifications()
