





import {Component, Vue} from "vue-property-decorator"
import PushNotifications from "@/managers/pushNotifications"
import NotificationMessage from "@/components/NotificationMessage.vue"

@Component({ components: { NotificationMessage } })
export default class PNotifications extends Vue {
	public message: string | null = null

	public created(): void {
		PushNotifications.onMessage = payload => {
			this.message = payload.notification.body
		}
	}

	public mounted(): void {
		this.enableNotifications()
	}

	public enableNotifications(): void {
		if (PushNotifications.isSupported && PushNotifications.isReady && !PushNotifications.isEnabled)
			PushNotifications.requestPermission()
				.then(wasGranted => {
					if (wasGranted)
						PushNotifications.enableNotifications()
				})
	}
}
